/*--------------------------------------------------------------
# Blockquotes Alt

1. Reset of various elements.
2. New Blockquote, mobile
3. New Blockquote, desktop
--------------------------------------------------------------*/
blockquote {
  border-left: none;
  font-weight: inherit;
  padding-left: 0;
  max-width: none;
}

blockquote:before {
  display: none;
}

/*------------------------------------------------------------------
2. Blockquote, mobile
------------------------------------------------------------------- */
.uds-blockquote {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 1.5rem;
  font-size: 1.25rem;
  font-weight: 700;
}
.uds-blockquote svg {
  grid-column: 1;
  fill: #191919;
  width: 2.5rem;
  height: auto;
}
.uds-blockquote blockquote {
  grid-column: 2;
  margin-top: 0;
  margin-bottom: 0;
}
