  margin-top: 1.5rem;
}
.uds-blockquote.accent-gold svg {
  fill: #ffc627;
}
.uds-blockquote.accent-maroon svg {
  fill: #8c1d40;
}

/*------------------------------------------------------------------
3. Blockquote, mobile only
------------------------------------------------------------------- */
@media (max-width: 767.98px) {
