/*--------------------------------------------------------------
Unity hero: highlight/subtitle covers up letters fix

The line height on hero H1's was not large enough to accomodate
highlighted text, leading to letters with descenders ('g', 'y',
etc.) being clipped. Upping the line height here to the mimimum
size that will prevent clipping.

Note: This is for all sizes except extra small and small, which
are handled with the media query below
---------------------------------------------------------------*/
div[class^=uds-hero] h1 {
  line-height: 4.5rem;
}

div[class^=uds-hero] [role=doc-subtitle] {
  line-height: 1.7rem;
}

/*--------------------------------------------------------------
Unity Hero Block Title/Subtitle Z-index

The unity hero block loses the z-index setting on the title and
subtitle when it drops below 768px (don't know why). Adding it
back.
---------------------------------------------------------------*/
@media (max-width: 768px) {
  div[class^=uds-hero] .btn-row,
div[class^=uds-hero] .content,
div[class^=uds-hero] [role=doc-subtitle],
div[class^=uds-hero] a.btn,
div[class^=uds-hero] a.uds-modal-close-btn,
div[class^=uds-hero] h1 {
    z-index: 30;
  }

  /* Unity hero also decides to drop the background gradient
  at this size. Adding it back. Style rule copied directly from
  the inspector at larger screen size. */
  div[class^=uds-hero]::after {
    content: "";
    height: 100%;
    width: 100%;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#19191900), to(#191919c9));
    background-image: -webkit-linear-gradient(top, #19191900 0, #191919c9 100%);
    background-image: -moz-linear-gradient(top, #19191900 0, #191919c9 100%);
    background-image: -o-linear-gradient(top, #19191900 0, #191919c9 100%);
    background-image: linear-gradient(180deg, #19191900 0, #191919c9 100%);
    grid-column: 1/span 3;
    grid-row: 1/span 4;
    z-index: 20;
  }

  /* The unity hero bock only scaled the font size down at the
  smallest mobile size (XSmall). Adding these rules to this
  media query so we will scale down the font size at XS and Small.

  Note: because we had an issue with highlighted text and line
  height, we set the line height manually here, and did not use
  a unity theme variable.

  Note: We're also using slightly smaller than standard text for our
  hero H1's here, only because it looks a little better IMO.
  */
  div[class^=uds-hero] h1 {
    font-size: 2rem;
    /* 2 rem */
    line-height: 2.25rem;
  }
}
@media only screen and (min-width: 576px) and (max-width: 768px) and (max-width: 768px) {
  div[class^=uds-hero] .content {
    width: 100%;
    max-width: 560px;
